'use client';

import { NeonGradientCard } from '@/components/ui/neon-gradient-card';
import { FileText, Landmark, Bell, User } from 'lucide-react';
import { motion } from 'motion/react';
import { useEffect, useState } from 'react';
import type { Profile, PdsSubmission } from '@smartgov/mock-data';

interface DashboardStatsProps {
  profile: Profile;
  pdsSubmissions?: PdsSubmission[];
  loading?: boolean;
}

interface StatCardProps {
  icon: React.ElementType;
  label: string;
  value: number | string;
  subtext?: string;
  index: number;
  neonColors?: { firstColor: string; secondColor: string };
}

function StatCard({ icon: Icon, label, value, subtext, index, neonColors }: StatCardProps) {
  const [count, setCount] = useState(0);
  const targetValue = typeof value === 'number' ? value : parseInt(value) || 0;

  // Animate number on mount
  useEffect(() => {
    let startTime: number;
    const duration = 1000; // 1 second animation

    const animate = (currentTime: number) => {
      if (!startTime) startTime = currentTime;
      const elapsed = currentTime - startTime;
      const progress = Math.min(elapsed / duration, 1);

      setCount(Math.floor(progress * targetValue));

      if (progress < 1) {
        requestAnimationFrame(animate);
      }
    };

    requestAnimationFrame(animate);
  }, [targetValue]);

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3, delay: index * 0.1 }}
    >
      <NeonGradientCard
        className="h-full"
        borderSize={2}
        borderRadius={16}
        neonColors={neonColors}
      >
        <div className="flex flex-col gap-4 p-6">
          {/* Icon */}
          <div className="flex h-12 w-12 items-center justify-center rounded-lg bg-gradient-to-br from-blue-600 to-indigo-600 text-white shadow-lg">
            <Icon className="h-6 w-6" />
          </div>

          {/* Label */}
          <div className="space-y-1">
            <p className="text-sm font-medium text-slate-600 dark:text-slate-400">
              {label}
            </p>
            <div className="flex items-baseline gap-2">
              <span className="text-3xl font-bold text-slate-900 dark:text-slate-100">
                {typeof value === 'number' ? count : value}
                {typeof value === 'number' && targetValue > 0 && targetValue > count && (
                  <span className="text-base text-slate-400">...</span>
                )}
              </span>
            </div>
            {subtext && (
              <p className="text-xs text-slate-500 dark:text-slate-500">
                {subtext}
              </p>
            )}
          </div>
        </div>
      </NeonGradientCard>
    </motion.div>
  );
}

export function DashboardStats({ profile, pdsSubmissions = [], loading = false }: DashboardStatsProps) {
  // Calculate statistics
  const pdsStatus = pdsSubmissions.length > 0
    ? pdsSubmissions[0]?.status?.toUpperCase() || 'NOT SUBMITTED'
    : 'NOT SUBMITTED';

  // Mock SALN status (would come from actual SALN data)
  const salnStatus = 'PENDING';

  // Calculate pending actions (mock data - would come from actual notifications/tasks)
  const pendingActions = 3;

  // Calculate profile completeness based on profile data
  const calculateProfileCompleteness = () => {
    const fields = [
      profile.firstName,
      profile.lastName,
      profile.middleName,
      profile.employeeId,
      profile.departmentId,
      profile.positionId,
    ];
    const filled = fields.filter(field => field !== null && field !== undefined && field !== '').length;
    return Math.round((filled / fields.length) * 100);
  };

  const profileCompleteness = calculateProfileCompleteness();

  const stats = [
    {
      icon: FileText,
      label: 'e-PDS Status',
      value: pdsStatus,
      subtext: pdsSubmissions.length > 0 ? `Version ${pdsSubmissions[0]?.version}` : 'No submissions yet',
      neonColors: { firstColor: '#3b82f6', secondColor: '#8b5cf6' },
    },
    {
      icon: Landmark,
      label: 'e-SALN Status',
      value: salnStatus,
      subtext: '2025 submission',
      neonColors: { firstColor: '#8b5cf6', secondColor: '#ec4899' },
    },
    {
      icon: Bell,
      label: 'Pending Actions',
      value: pendingActions,
      subtext: 'Items requiring attention',
      neonColors: { firstColor: '#ec4899', secondColor: '#f59e0b' },
    },
    {
      icon: User,
      label: 'Profile Completeness',
      value: `${profileCompleteness}%`,
      subtext: profileCompleteness === 100 ? 'All fields complete' : 'Complete your profile',
      neonColors: { firstColor: '#f59e0b', secondColor: '#10b981' },
    },
  ];

  if (loading) {
    return (
      <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
        {[1, 2, 3, 4].map((i) => (
          <div
            key={i}
            className="h-40 animate-pulse rounded-2xl bg-slate-200 dark:bg-slate-800"
          />
        ))}
      </div>
    );
  }

  return (
    <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
      {stats.map((stat, index) => (
        <StatCard key={stat.label} {...stat} index={index} />
      ))}
    </div>
  );
}
