'use client';

import { BorderBeam } from '@/components/ui/border-beam';
import { ThemeToggle } from '@/components/theme/ThemeToggle';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { Badge } from '@/components/ui/badge';
import { Calendar, Briefcase } from 'lucide-react';
import type { Profile, Position } from '@smartgov/mock-data';

interface DashboardWelcomeProps {
  profile: Profile;
  position: Position | null;
}

export function DashboardWelcome({ profile, position }: DashboardWelcomeProps) {
  const getInitials = () => {
    const firstInitial = profile.firstName?.[0]?.toUpperCase() || '';
    const lastInitial = profile.lastName?.[0]?.toUpperCase() || '';
    return `${firstInitial}${lastInitial}`;
  };

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return 'Good morning';
    if (hour < 18) return 'Good afternoon';
    return 'Good evening';
  };

  const getCurrentDate = () => {
    return new Date().toLocaleDateString('en-US', {
      weekday: 'long',
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };

  return (
    <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-blue-600 via-blue-700 to-indigo-600 p-6 sm:p-8 text-white shadow-2xl">
      <BorderBeam size={250} duration={12} delay={9} colorFrom="#60a5fa" colorTo="#818cf8" />

      {/* Background Effects */}
      <div className="absolute top-0 right-0 w-64 h-64 bg-white/10 rounded-full blur-3xl"></div>
      <div className="absolute bottom-0 left-0 w-48 h-48 bg-indigo-400/20 rounded-full blur-3xl"></div>

      {/* Theme Toggle - Top Right */}
      <div className="absolute top-4 right-4 z-20">
        <ThemeToggle variant="minimal" size="md" className="bg-white/10 hover:bg-white/20 border-white/20 text-white" />
      </div>

      {/* Content */}
      <div className="relative z-10 flex flex-col sm:flex-row items-start gap-6">
        {/* Avatar */}
        <div className="relative">
          <Avatar className="h-16 w-16 sm:h-20 sm:w-20 border-4 border-white/20 shadow-xl ring-4 ring-blue-400/30">
            <AvatarFallback className="bg-gradient-to-br from-blue-400 to-indigo-500 text-white text-2xl sm:text-3xl font-bold">
              {getInitials()}
            </AvatarFallback>
          </Avatar>
          {profile.isActive && (
            <div className="absolute bottom-0 right-0 h-4 w-4 rounded-full bg-green-400 border-2 border-white shadow-lg"></div>
          )}
        </div>

        {/* Welcome Text */}
        <div className="flex-1">
          <div className="flex items-center gap-2 mb-2">
            <Calendar className="h-4 w-4 text-blue-200" />
            <p className="text-sm text-blue-100">{getCurrentDate()}</p>
          </div>

          <h1 className="text-2xl sm:text-3xl font-bold mb-2">
            {getGreeting()}, {profile.firstName}!
          </h1>

          <div className="flex flex-wrap items-center gap-2 mb-3">
            <Badge variant="outline" className="bg-white/10 text-white border-white/30 backdrop-blur-sm">
              <Briefcase className="h-3 w-3 mr-1" />
              {position?.title || 'Government Employee'}
            </Badge>
            <Badge variant="outline" className="bg-white/10 text-white border-white/30 backdrop-blur-sm">
              ID: {profile.employeeId}
            </Badge>
          </div>

          <p className="text-blue-100 text-sm sm:text-base">
            Welcome to your SmartGov dashboard. Manage your e-PDS and e-SALN submissions efficiently.
          </p>
        </div>
      </div>
    </div>
  );
}
