'use client';

import { MagicCard } from '@/components/ui/magic-card';
import { cn } from '@/lib/utils';
import { FileText, Landmark, User, Settings, ArrowRight } from 'lucide-react';
import { motion } from 'motion/react';
import { useRouter } from 'next/navigation';

interface QuickAction {
  id: string;
  title: string;
  description: string;
  icon: React.ElementType;
  href: string;
  gradientFrom: string;
  gradientTo: string;
}

const quickActions: QuickAction[] = [
  {
    id: 'pds',
    title: 'e-PDS',
    description: 'View or update your Personal Data Sheet',
    icon: FileText,
    href: '/dashboard/pds',
    gradientFrom: '#3b82f6',
    gradientTo: '#8b5cf6',
  },
  {
    id: 'saln',
    title: 'e-SALN',
    description: 'Submit your Statement of Assets, Liabilities, and Net Worth',
    icon: Landmark,
    href: '/dashboard/saln',
    gradientFrom: '#8b5cf6',
    gradientTo: '#ec4899',
  },
  {
    id: 'profile',
    title: 'Update Profile',
    description: 'Manage your personal information and settings',
    icon: User,
    href: '/dashboard/profile',
    gradientFrom: '#ec4899',
    gradientTo: '#f59e0b',
  },
  {
    id: 'settings',
    title: 'Settings',
    description: 'Configure your account preferences',
    icon: Settings,
    href: '/dashboard/settings',
    gradientFrom: '#f59e0b',
    gradientTo: '#10b981',
  },
];

interface QuickActionCardProps {
  action: QuickAction;
  index: number;
  onClick: () => void;
}

function QuickActionCard({ action, index, onClick }: QuickActionCardProps) {
  const Icon = action.icon;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3, delay: index * 0.1 }}
      className="h-full"
    >
      <MagicCard
        className="h-full cursor-pointer transition-all duration-300 hover:scale-[1.02]"
        gradientSize={300}
        gradientColor="#1e293b"
        gradientOpacity={0.15}
        gradientFrom={action.gradientFrom}
        gradientTo={action.gradientTo}
      >
        <button
          onClick={onClick}
          className="group relative flex h-full w-full flex-col gap-4 p-6 text-left transition-all"
          aria-label={`Navigate to ${action.title}`}
        >
          {/* Icon with gradient background */}
          <div
            className={cn(
              "flex h-12 w-12 items-center justify-center rounded-xl shadow-lg transition-transform duration-300 group-hover:scale-110",
              "bg-gradient-to-br text-white"
            )}
            style={{
              backgroundImage: `linear-gradient(to bottom right, ${action.gradientFrom}, ${action.gradientTo})`,
            }}
          >
            <Icon className="h-6 w-6" />
          </div>

          {/* Content */}
          <div className="flex-1 space-y-2">
            <h3 className="text-lg font-bold text-slate-900 dark:text-slate-100 transition-colors">
              {action.title}
            </h3>
            <p className="text-sm text-slate-600 dark:text-slate-400 transition-colors">
              {action.description}
            </p>
          </div>

          {/* Arrow icon - appears on hover */}
          <div className="flex items-center justify-end">
            <ArrowRight
              className={cn(
                "h-5 w-5 text-slate-400 transition-all duration-300",
                "group-hover:translate-x-1 group-hover:text-slate-900 dark:group-hover:text-slate-100"
              )}
            />
          </div>
        </button>
      </MagicCard>
    </motion.div>
  );
}

export function QuickActions() {
  const router = useRouter();

  const handleActionClick = (href: string) => {
    router.push(href);
  };

  return (
    <div className="space-y-4">
      {/* Header */}
      <div>
        <h2 className="text-2xl font-bold text-slate-900 dark:text-slate-100">
          Quick Actions
        </h2>
        <p className="text-sm text-slate-600 dark:text-slate-400">
          Access frequently used features
        </p>
      </div>

      {/* Actions Grid */}
      <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-2">
        {quickActions.map((action, index) => (
          <QuickActionCard
            key={action.id}
            action={action}
            index={index}
            onClick={() => handleActionClick(action.href)}
          />
        ))}
      </div>
    </div>
  );
}
