interface DeadlineItemProps {
  deadline: Deadline;
  index: number;
}

function DeadlineItem({ deadline, index }: DeadlineItemProps) {
  const daysUntilDue = differenceInDays(deadline.dueDate, new Date());
  const isOverdue =
    isBefore(deadline.dueDate, new Date()) && !deadline.completed;
  const isUrgent = daysUntilDue <= 7 && !deadline.completed;

  const priorityStyles = {
    urgent:
      'border-red-200 bg-red-50 dark:border-red-900/30 dark:bg-red-950/20',
    high: 'border-amber-200 bg-amber-50 dark:border-amber-900/30 dark:bg-amber-950/20',
    normal:
      'border-blue-200 bg-blue-50 dark:border-blue-900/30 dark:bg-blue-950/20',
  };

  const badgeVariants = {
    urgent: 'bg-red-100 text-red-700 dark:bg-red-950/50 dark:text-red-400',
    high: 'bg-amber-100 text-amber-700 dark:bg-amber-950/50 dark:text-amber-400',
    normal: 'bg-blue-100 text-blue-700 dark:bg-blue-950/50 dark:text-blue-400',
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: -10 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ duration: 0.2, delay: index * 0.05 }}
      className={cn(
        'relative rounded-lg border p-4 transition-all',
        deadline.completed
          ? 'border-green-200 bg-green-50/50 dark:border-green-900/30 dark:bg-green-950/10 opacity-60'
          : priorityStyles[deadline.priority]
      )}>
      <div className="flex items-start gap-3">
        {/* Status Icon */}
        <div className="flex-shrink-0 mt-0.5">
          {deadline.completed ? (
            <CheckCircle2 className="h-5 w-5 text-green-600 dark:text-green-500" />
          ) : isOverdue || isUrgent ? (
            <AlertTriangle className="h-5 w-5 text-red-600 dark:text-red-500" />
          ) : (
            <Calendar className="h-5 w-5 text-blue-600 dark:text-blue-500" />
          )}
        </div>

        {/* Content */}
        <div className="flex-1 space-y-2">
          <div className="flex items-start justify-between gap-2">
            <h4
              className={cn(
                'font-semibold text-sm',
                deadline.completed
                  ? 'text-slate-600 dark:text-slate-400 line-through'
                  : 'text-slate-900 dark:text-slate-100'
              )}>
              {deadline.title}
            </h4>
            {!deadline.completed && (
              <Badge
                variant="secondary"
                className={cn(
                  'flex-shrink-0 text-xs',
                  badgeVariants[deadline.priority]
                )}>
                {isOverdue ? 'Overdue' : `${daysUntilDue}d left`}
              </Badge>
            )}
          </div>
          <p className="text-xs text-slate-600 dark:text-slate-400">
            {deadline.description}
          </p>
          <div className="flex items-center gap-2 text-xs text-slate-500 dark:text-slate-500">
            <Calendar className="h-3 w-3" />
            <span>Due: {format(deadline.dueDate, 'MMM dd, yyyy')}</span>
          </div>
        </div>
      </div>
    </motion.div>
  );
}

interface TipItemProps {
  tip: Tip;
  index: number;
}

function TipItem({ tip, index }: TipItemProps) {
  const categoryColors = {
    security: 'text-red-600 dark:text-red-400',
    productivity: 'text-blue-600 dark:text-blue-400',
    compliance: 'text-amber-600 dark:text-amber-400',
    feature: 'text-green-600 dark:text-green-400',
  };

  const categoryBg = {
    security: 'bg-red-100 dark:bg-red-950/30',
    productivity: 'bg-blue-100 dark:bg-blue-950/30',
    compliance: 'bg-amber-100 dark:bg-amber-950/30',
    feature: 'bg-green-100 dark:bg-green-950/30',
  };

  return (
    <motion.div
      initial={{ opacity: 0, x: -10 }}
      animate={{ opacity: 1, x: 0 }}
      transition={{ duration: 0.2, delay: index * 0.05 }}
      className="group relative rounded-lg border border-slate-200 dark:border-slate-800 bg-slate-50/50 dark:bg-slate-900/50 p-4 transition-all hover:shadow-md hover:border-blue-300 dark:hover:border-blue-800">
      <div className="flex gap-3">
        {/* Icon */}
        <div
          className={cn(
            'flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-lg transition-colors',
            categoryBg[tip.category]
          )}>
          <Lightbulb className={cn('h-4 w-4', categoryColors[tip.category])} />
        </div>

        {/* Content */}
        <div className="flex-1 space-y-1">
          <h4 className="font-semibold text-sm text-slate-900 dark:text-slate-100">
            {tip.title}
          </h4>
          <p className="text-xs text-slate-600 dark:text-slate-400 leading-relaxed">
            {tip.description}
          </p>
          <Badge
            variant="secondary"
            className="mt-2 text-xs capitalize bg-slate-200 dark:bg-slate-800 text-slate-700 dark:text-slate-300">
            {tip.category}
          </Badge>
        </div>
      </div>
    </motion.div>
  );
}

