'use client';

import { DashboardWelcome } from '@/components/dashboard/profile/DashboardWelcome';
import { DashboardStats } from '@/components/dashboard/profile/DashboardStats';
import { QuickActions } from '@/components/dashboard/profile/QuickActions';
import { RecentActivity } from '@/components/dashboard/profile/RecentActivity';
import { DashboardOverview } from '@/components/dashboard/profile/DashboardOverview';
import {
  mockProfiles,
  mockPositions,
  mockDepartments,
  getPdsSubmissionsByUserId,
} from '@smartgov/mock-data';

export default function DashboardPage() {
  // Mock data - In production, this would come from your API/database
  const currentUserId = '01927d4e-8b45-7f52-b123-456789abcdef'; // Juan dela Cruz
  const profile =
    mockProfiles.find((p) => p.id === currentUserId) || mockProfiles[0];
  const position =
    mockPositions.find((p) => p.id === profile.positionId) || null;
  const department =
    mockDepartments.find((d) => d.id === profile.departmentId) || null;
  const pdsSubmissions = getPdsSubmissionsByUserId(currentUserId);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100 dark:from-slate-900 dark:via-slate-800 dark:to-slate-900">
      <div className="mx-auto max-w-7xl space-y-6 p-4 sm:p-6 lg:p-8">
        {/* Welcome Section */}
        <DashboardWelcome profile={profile} position={position} />
        {/* Stats Section */}
        <DashboardStats profile={profile} pdsSubmissions={pdsSubmissions} />
        {/* Quick Actions Section */}
        <QuickActions />
        {/* Overview Section - Deadlines and Tips */}
        <DashboardOverview />/{/* Recent Activity Section */}
        <RecentActivity maxItems={6} showViewAll={true} />
      </div>
    </div>
  );
}
